.detail_shipping {
	width: 100%;
	position: relative;
	margin-bottom: 30px;
	div {
		display: flex;
		justify-content: space-between;
	}

	.list-group > .text-1,
	.list-group > .text-1-1,
	.list-group > .text-2,
	.header {
		background-color: #f1f1f1;
		border: none;
		font-size: 1.2rem;
	}

	.list-group > .list-group-item > span:first-child {
		width: 50%;
	}

	.list-group > .list-group-item > .text-3 {
		font-weight: bold;
	}
	.list-group > .list-group-item > .text-3-1 {
		opacity: 0.6;
	}

	.list-group > .list-group-item > .text-3-1-1 {
		opacity: 0.6;
		font-weight: bold;
	}

	.list-group > .group-2 {
		padding-bottom: 0px;
	}

	.list-group > .text-4 {
		padding: 30px 20px 20px 20px;

		.text-4-1 {
			color: #111;
			font-size: 1.2rem;
		}
		.text-4-2 {
			font-size: 1.3rem;
			color: #f15e2c;
		}
	}

	.header {
		font-size: 1.4rem;
		font-weight: bold;
		padding: 10px 20px;
		margin: 40px 0 0 0;
	}

	.line {
		padding: 0 20px;
		margin: 0 20px;
		border-bottom: 2px solid #123;
	}
	.dash {
		padding: 0 20px;
		margin: 0 20px;
		border-bottom: dashed;
		border-width: 2px;
		opacity: 0.6;
	}
	.btnComplete {
		width: 100%;
		margin: 0;
		background-color: #f1f1f1;
		button {
			font-size: 1.25rem;
			width: 100%;
			height: 70px;
			background-color: #f15e2c;
			color: #fff;
			font-weight: bold;
			border-radius: 0px;
		}
	}
}
